
import { Entry } from "@/models/Chapter";
import { Options, Vue } from "vue-class-component";

@Options({
  name: "Section",
  props: {
    entry: Object,
  },
})
export default class Section extends Vue {
  private entry!: Entry;
  private section = "";

  mounted(): void {
    this.section = this.entry.parameters[0];
  }
}
